<template>
  <div class="app-main">
    <transition name="fade-transform" mode="out-in">
      <router-view :key="key" />
    </transition>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    key() {
      return this.$route.path
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/style/common.scss";
.app-main {
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
  font-size:32px;
  position: relative;
  // overflow: hidden;
}
.page-container{
  height: 100vh;
  background-color:$cl-white;
  overflow: hidden;
  position: relative;
}

/* fade-transform */
.fade-transform-leave-active,
.fade-transform-enter-active {
  transition: all .5s;
}

.fade-transform-enter {
  opacity: 0;
  transform: translateX(30px);
}

.fade-transform-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}

/* outin-transform */
.outin-transform-leave-active,
.outin-transform-enter-active {
  transition: all .5s;
}

.outin-transform-enter,
.outin-transform-leave-to {
  transform: translateX(-100%);
}
</style>


