import Vue from 'vue'
import Component from './func-messagebox'

const messageboxConstructor = Vue.extend(Component)

const messagebox = options => {
	if(Vue.prototype.$isServer) return

	const instance = new messageboxConstructor({
		propsData:options
  })

  instance.vm = instance.$mount()
	document.body.appendChild(instance.vm.$el)

	instance.vm.$on('hide', () => {
		document.body.removeChild(instance.vm.$el)
		instance.vm.$destroy()
	})

	instance.vm.$on('show',()=>{
		instance.vm.show = true
	})

	instance.vm.$on('submit',()=>{
    instance.vm.onClose && instance.vm.onClose()
		document.body.removeChild(instance.vm.$el)
    instance.vm.$destroy()
	})

	return instance.vm
}

export default messagebox



