import Vue from 'vue'
import App from './App'
import router from './router'

import 'lib-flexible/flexible'

import notify from '@/components/Notification'
import messagebox from '@/components/MessageBox'

import { setStore, getStore,getUrlParam } from '@/assets/js/utils'

Vue.use(notify)
Vue.use(messagebox)

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  let userId = to.query.userId || getUrlParam('userId')
  if(userId) {
    setStore('LEIDA_USERID',userId)
  }else{
    userId = getStore('LEIDA_USERID')
  }
  if(userId){
    next()
  }else{
    try{
      // 跳转app内登录
      window.location.href="gkld://gongkaoleida/appLogin"
    }catch(err){
      console.warn(err)
    }
  }
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



