<template>
  <div class="m-messagebox" v-show="show">
    <div class="container">
      <h3 class="title">{{title}}</h3>
      <p class="desc">{{text}}</p>
      <div class="submit">
        <div class="items" @click="hideMessageBox">取消</div>
        <div class="items highlight" @click="submitMessageBox">确定</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'messagebox',
  props: {
    show: {
      type: Boolean,
      default: true
    },
    title:{
      type:String,
      default:'提示'
    },
    text: {
      type: String,
      default: "程序小哥哥提醒你"
    },
    onClose:{   // 关闭后的回调
      type:Function,
      default:()=> null
    } 
  },
  methods: {
    hideMessageBox() {
      this.$emit("hide")
    },
    showMessageBox() {
      this.$emit("show")
    },
    submitMessageBox(){
      this.$emit('submit',this.onClose)
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/style/variable.scss";
.m-messagebox {
  position: fixed;
  left:0;
  top:0;
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,.7);
  z-index: 10;  
  display: flex;
  justify-content: center;
  align-items: center;
  .container {
    width:600px; 
    background-color:$cl-white;
    border-radius:10px;
    overflow: hidden;  
  }
  .title{
    margin-top:40px;
    font-size:36px;
    text-align: center;
  }
  .desc {
    font-size:30px;
    margin:36px 50px;
  }
  .submit{
    display: flex;
    align-items: center;
    .items{
      flex:1;
      height: 90px;
      text-align: center;
      line-height: 90px;
      background-color:#fff;
      font-size:30px;      
      &.highlight{
        background-color:$cl-theme;
        color:$cl-white;
      }
    }
  }
}
</style>



