import Vue from 'vue'
import Router from 'vue-router'

const home = () => import ( /* webpackChunkName:'home'*/ '@/pages/home')
const subject = () => import ( /* webpackChunkName:'subject'*/ '@/pages/subject')
const share = () => import ( /* webpackChunkName:'share'*/ '@/pages/share')
const result = () => import ( /* webpackChunkName:'result'*/ '@/pages/result')

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/subject',
      name: 'subject',
      component: subject
    },
    {
      path: '/share',
      name: 'share',
      component: share
    },
    {
      path: '/result',
      name: 'result',
      component: result
    }
  ]
})



