<template>
  <transition name="drop">
    <div class="m-notify" v-show="notifyVisible" @click.stop="hide">
      <div class="desc">{{content}}</div>
    </div>
  </transition>
</template>

<script>
export default {
  name:'notifycation',
  props: {
    delay: {
      type: Number,
      default: 2000
    },
    content: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      notifyVisible: false
    };
  },
  methods: {
    show() {
      this.notifyVisible = true;
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.hide();
        this.$emit("closed");
      }, this.delay);
    },
    hide() {
      this.notifyVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.m-notify {
  position: fixed;
  bottom:250px;
  left:0;
  width:100%;
  text-align: center;
  z-index: 10;  
  &.drop-enter-active,
  &.drop-leave-active {
    transition: all 0.1s;
    .desc {
      transition: all 0.3s;
    }
  }
  &.drop-enter,
  &.drop-leave-to {
    opacity: 0;
    .desc {
      transform: translate3d(0, -100%, 0);
    }
  }
  .desc {
    display: inline-block;
    padding:30px 50px;
    background-color: rgba(0, 0, 0, .7);
    border-radius: 90px;
    color: #fff;
    font-size: 30px;
  }
}
</style>



