// 获取url中的参数
export function getUrlParam(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
    var r = window.location.search.substr(1).match(reg);  //匹配目标参数
    if (r != null) return unescape(r[2]); return null; //返回参数值
}
// 存储localStorage
export const setStore = (name, content) => {
    if (!name) return
    if (typeof content !== 'string') {
        content = JSON.stringify(content)
    }
    window.localStorage.setItem(name, content)
}

// 获取localStorage
export const getStore = name => {
    if (!name) return
    return window.localStorage.getItem(name)
}

// 删除localStorage
export const removeStore = name => {
    if (!name) return
    window.localStorage.removeItem(name)
}
// 时间戳格式化 时:分:秒
export const formatTime = time =>{
  let h = time > 3600 || time == 3600 ? Math.floor(time / 3600) : '00'
  let m = time > 3600 || time == 3600 ? Math.floor((time - h * 3600) / 60) : Math.floor(time / 60)
  let s = time > 3600 || time == 3600 ? (time - h * 3600) % 60 : time % 60
  let hh = formatString(h,2)
  let mm = formatString(m,2)
  let ss = formatString(s,2)
  let str = mm + '分' + ss + '秒'
  return time > 3600 || time == 3600 ? hh + "时" + str : str
}
// 格式化两位数字 补0
export const formatString = (num,n) =>{
	let len = num.toString().length
	while(len < n){
		num = '0'+num
		len++
	}
	return num
}
// 文本替换
export function replaceContent(text){
  if(toString.call(text) === '[object String]'){
    let t = text.replace(/(\r\n)|(\n)|\\n/g,'<br>')
    t = t.replace(/\s/g,' ')
    return t
  }else{
    return text
  }
}

// 防抖函数
export function debounce(handle,delay){
  var timer = null;
  return function(){
    var _this = this,
    _arg = arguments;
    clearTimeout(timer);
    timer = setTimeout(function(){
      handle.apply(_this,_arg)
    },delay)
  }
}


